<template>
  <div class="message-list">
    <ol class="list-wrapper">
      <router-link
        :to="{ name: 'MessageDetail', query: {id: topic.pk} }"
        tag="li"
        class="list-item"
        v-for="(topic, i) of topic.list"
        :key="i"
      >
        <div class="header">{{ topic.subject }}</div>
        <div class="footer">
          <span>{{ topic.day }}</span>
          <span>{{ topic.weekDay }}</span>
          <span>{{ topic.time }}</span>
          <span>{{ topic.name }}</span>
        </div>
      </router-link>
    </ol>
  </div>
</template>

<script>
import {jbxx1QuerydataZymhlist} from '../assets/js/api';
export default {
  data() {
    return {
      topic: {
        list: []
      }
    };
  },
  mounted() {
    jbxx1QuerydataZymhlist().then(res => {
      this.topic.list = res.data.map(o => ({
        ...o,
        subject: o.title,
        day: o.createTime,
        weekDay: '',
        time: '',
        name: o.zgr
      }));
    });
  }
};
</script>

<style lang="less" scoped>
.message-list {
  background: #f5f5f5;
  padding: 20px;
  .list-wrapper {
    .list-item {
      background-color: #ffffff;
      box-shadow: 3px 4px 9px 1px rgba(1, 88, 155, 0.05);
      border-radius: 8px;
      padding: 26px;
      &:not(:first-child) {
        margin-top: 20px;
      }
      .header {
        font-size: 36px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 40px;
        color: #333333;
      }
      .footer {
        margin-top: 28px;
        font-size: 28px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 46px;
        letter-spacing: 0px;
        color: #888888;
        > span:not(:last-child) {
          margin-right: 20px;
        }
      }
    }
  }
}
</style>



